<template>
  <div class="banner">
    <swiper ref="mySwiper" :options="swiperOption">
      <swiper-slide>
         <img class="swiper-img" :src="require('@/assets/images/mobile/index.bg.png')" alt="" srcset="">
      </swiper-slide>
      <!-- <div class="swiper-pagination" slot="pagination"></div> -->
    </swiper>
  </div>
</template>

<script>
// @ts-ignore
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";
  export default {
  components: {
      swiper,
      swiperSlide
  },
  data () {
    return {
      swiperOption: {
        loop: false,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },// 显示分页
        pagination: {
          el: ".swiper-pagination",
          clickable: false //允许分页点击跳转
        },// 设置点击箭头
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  mounted () {
    // this.swiper.slideTo(0, 1000, true)
  }
  }
</script>

<style lang="scss" scoped>
@import '~@/assets/styles/var.scss';
.banner{
  width: 100%;
  overflow-y: hidden;
}
 .swiper-container{
  position: relative;
  width: 100%;
  height: toPx(850);
  box-sizing: border-box;
}
.swiper-img{
  width: 100%;
  vertical-align: top;
}
.swiper-slide{
  width: 100%;
  line-height: toPx(200);
  background: #ffffff;
  color: #000;
  font-size: 16px;
  text-align: center;
}
</style>